:root {
  --arm-angle: 0deg;
  --base-size: 10px;
  --font-size-big: calc(2.5 * var(----base-size));
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  color: #d3d3d3;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: calc(2 * var(--base-size));
  background: linear-gradient(#090a0c, #040506);
  position: absolute;
}

.night {
  background: linear-gradient(#040506, #000);
}

#helpIcon {
  cursor: pointer;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 10px;
  right: 10px;
}

#helpIcon .background {
  clip-path: circle(50%);
  width: 100%;
  height: 100%;
  background: radial-gradient(#6970bf, #151528);
  position: absolute;
}

#helpIcon .icon {
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 35px;
  position: absolute;
}

#helpIcon.fadeout {
  animation: 3s linear fadeout;
}

#helpIcon.fadein {
  animation: 2s linear reverse fadeout;
}

#helpIcon.fadein .background {
  animation: .5s linear expand;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes expand {
  from {
    clip-path: circle(0%);
  }

  to {
    clip-path: circle(50%);
  }
}

#welcome {
  color: #a5c1ee;
  font-size: var(--font-size-big);
  width: 36em;
  background-color: #084172;
  border: 7px solid #419cdc;
  border-radius: 28px;
  padding: 1em;
  position: absolute;
  top: 1.5em;
  left: calc(50% - 18em);
}

#welcome .closeIcon {
  cursor: pointer;
  position: absolute;
  top: 8px;
  right: 10px;
}

#welcome header {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: calc(3 * var(--base-size));
  text-align: center;
}

#welcome label {
  margin-bottom: 5px;
}

#welcome label input {
  display: block;
}

#welcome input {
  font-size: var(--font-size-big);
  padding: 5px;
}

#welcome button {
  color: #97c997;
  cursor: pointer;
  font-size: var(--font-size-big);
  background-color: green;
  border: 3px solid #8fbd8f;
  border-radius: 6px;
  padding: 8px 24px;
  font-weight: 600;
}

#welcome button:hover {
  color: #007500;
  background: linear-gradient(to top, green, #90ee90);
}

.info {
  text-align: center;
  background: linear-gradient(to top, #07488d, #29a8db);
  border: 2px solid #809bff;
  border-radius: 5px;
  padding: 1px 10px;
  display: inline-block;
}

#coordinatesForm {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.chilloclock {
  clip-path: circle(50%);
  width: calc(50 * var(--base-size));
  height: calc(50 * var(--base-size));
  margin: 5em auto 0;
