.chilloclock .icon {
  font-size: calc(3 * var(--base-size));
  -webkit-user-select: none;
  user-select: none;
  position: absolute;
}

.chilloclock .sunrise {
  top: 50%;
  transform: translateY(-50%);
}

.chilloclock .sunset {
  top: 50%;
  left: 100%;
  transform: translate(-100%, -50%);
}

.chilloclock .sun {
  left: 50%;
